import { _decorator, Component, Node, Vec3, tween } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('scoreAn')
export class scoreAn extends Component {
    public targetPos: Vec3 = new Vec3()
    public jumpStep: number = 50
    scoreAc = null
    start() {
    }

    scoreAn() {
        if (this.scoreAc == null) {
            this.node.active = true
            Vec3.add(this.targetPos, this.node.position, new Vec3(0, this.jumpStep, 0))
            let outSide = tween().to(0.2, { position: this.targetPos }, { easing: 'linear' })
            this.scoreAc = tween(this.node).then(outSide).call(() => {
                this.scoreAc = null
                this.node.active = false
                this.node.setPosition(0, -403.081, 0)
                this.targetPos = new Vec3()
            }).start()
        } else {
            this.scoreAc.stop()
            this.scoreAc = null
            this.node.setPosition(0, -403.081, 0)
            this.targetPos = new Vec3()
            this.node.active = false
            this.reagin()
        }
    }

    reagin() {
        this.node.active = true
        Vec3.add(this.targetPos, this.node.position, new Vec3(0, this.jumpStep, 0))
        let outSide = tween().to(0.2, { position: this.targetPos }, { easing: 'linear' })
        this.scoreAc = tween(this.node).then(outSide).call(() => {
            this.scoreAc = null
            this.node.active = false
            this.node.setPosition(0, -403.081, 0)
            this.targetPos = new Vec3()
        }).start()
    }

    update(deltaTime: number) {

    }
}

